import React, { useContext, useEffect, useState } from 'react'
import { EstatePlanContext } from '../../State/EstatePlanStore'
import TodoItem from '../../Components/TodoItem'
import CryptoassetTypes from './CryptoassetTypes'
import CryptoassetsWallets from './CryptoassetsWallets'
import CryptoassetsHelpers from './CryptoassetsHelpers'
import CryptoassetsOtherThingsYouShouldKnow from './CryptoassetsOtherThingsYouShouldKnow'
import BasePage from '../BasePage'
import Documents from "../Documents"


const Cryptoassets = () => {
    const [cryptoAssetTypesDone, setCryptoAssetTypesDone] = useState(false)
    const [walletsDone, setWalletsDone] = useState(false)
    const [helpersDone, setHelpersDone] = useState(false)
    const [otherInformationDone, setOtherInformationDone] = useState(false)

    const [{ cryptoassets }] = useContext(EstatePlanContext)

    useEffect(() => {
        const {
            typesHeld,
            wallets,
            trustedHelpers,
            otherInformation,
        } = cryptoassets
        setCryptoAssetTypesDone(typesHeld && typesHeld.length > 0)
        setWalletsDone(wallets && wallets.length > 0)
        setHelpersDone(trustedHelpers && trustedHelpers.length > 0)
        setOtherInformationDone(otherInformation)
    }, [cryptoassets])

    const owlText = (
        <>
            I can help you set up a plan for your crypto. How do you want to
            start?
        </>
    )

    return (
        <BasePage owlSay={owlText}>

            <TodoItem done={cryptoAssetTypesDone} to={CryptoassetTypes.path}>
                Types of cryptoassets I own
            </TodoItem>
            <TodoItem done={walletsDone} to={CryptoassetsWallets.path}>
                Wallets I use
            </TodoItem>
            <TodoItem done={helpersDone} to={CryptoassetsHelpers.path}>
                Trusted helpers
            </TodoItem>
            <TodoItem
                done={otherInformationDone}
                to={CryptoassetsOtherThingsYouShouldKnow.path}>
                Other information
            </TodoItem>
            <TodoItem to={Documents.path}>
                Review and Print
            </TodoItem>
        </BasePage>
    )
}

Cryptoassets.path = '/cryptoassets'

export default Cryptoassets
