/**
 * Provides event handlers to hide and show default value in a field
 * @param def Value to be shown when the field is blank
 * @param cb Callback function, called with the value to be displayed
 * @returns {[ev => null, ev => null]}
 */

// eslint-disable-next-line
export default (def, cb) => {
    const handleHideDefault = ({target: {value}}) => value === def && cb('')
    const handleShowDefault = ({target: {value}}) => !value  && cb(def)
    return [handleHideDefault, handleShowDefault]
}