import React, { useContext , useState, useEffect} from 'react'
import { Button } from 'react-bootstrap'
import { Link, useHistory } from 'react-router-dom'
import { EstatePlanContext } from '../../State/EstatePlanStore'
import './CryptoassetsWallets.scss'
import BasePage from '../BasePage'
import CryptoassetsWallet from './CryptoassetsWallet'
import uuid from 'react-uuid'
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faUserSlash} from "@fortawesome/free-solid-svg-icons";
import {faPlus} from "@fortawesome/free-solid-svg-icons";
import {REMOVE_WALLET} from "../../State/EstatePlanReducer";
import CryptoassetsNav from "./CryptoassetsNav"
import PrevNextButtons from "../../Components/PrevNextButtons";
import CryptoassetTypes from "./CryptoassetTypes";
import CryptoassetsHelpers from './CryptoassetsHelpers'

const CryptoassetsWallets = () => {
    const history = useHistory()
    const [{ cryptoassets: { wallets } }, dispatch] = useContext(EstatePlanContext)
    const [orderedWallets, setOrderedWallets] = useState([])

    useEffect(
        () =>
            setOrderedWallets(
                wallets.sort((a, b) => a.name.localeCompare(b.name)),
            ),
        [wallets],
    )

    const owlText = (
        <>
            Let me get some information about the wallets you use to store
            cyptoassets.
        </>
    )

    const onAddWallet = () => {
        history.push(CryptoassetsWallet.path.replace(':id', uuid()))
    }

    const onRemove = id => () => dispatch({type: REMOVE_WALLET, id})

    return (
        <BasePage owlSay={owlText}>
            <CryptoassetsNav />
            {orderedWallets.length === 0 && (
                <h4 className={"text-center mb-4"}>
                    You haven't added any wallets yet.
                </h4>
            )}
            {orderedWallets.length > 0 && (
                <>
                    <h4 className={"XXXtext-center mb-4"}>
                        Your wallets:
                    </h4>

                    <ul className="WalletList">
                        {orderedWallets.map((w, i) => (
                            <li key={w.id}>
                                <FontAwesomeIcon
                                    icon={faUserSlash}
                                    onClick={onRemove(w.id)}
                                    className="delete-user-icon"
                                /> <Link to={CryptoassetsWallet.path.replace(':id', w.id)}>
                                    {w.name ? w.name : "[no name]"}
                                </Link>
                            </li>
                        ))}
                    </ul>
                </>
            )}
            <div className="text-center">
                <Button variant="primary" size="lg" onClick={onAddWallet} className="btn btn-primary">
                    <FontAwesomeIcon icon={faPlus} /> Add Wallet
                </Button>
            </div>
            <PrevNextButtons prev={CryptoassetTypes.path} next={CryptoassetsHelpers.path} />
        </BasePage>
    )
}
CryptoassetsWallets.path = '/cryptoassets/wallets'

export default CryptoassetsWallets
