import React, { useContext } from 'react'
import CryptoAssetInventoryForm from 'crypto-asset-inventory-plugin'
import { EstatePlanContext } from '../State/EstatePlanStore'
import { set } from 'backtickify/src/dataStore'
import ReactMarkdown from 'react-markdown'
import { jsPDF } from 'jspdf'
import { Button } from 'react-bootstrap'
import './Documents.scss'
import CryptoassetsNav from "./cryptoassets/CryptoassetsNav";
import BasePage from "./BasePage";
import PrevNextButtons from "../Components/PrevNextButtons";
import CryptoassetsOtherThingsYouShouldKnow from "./cryptoassets/CryptoassetsOtherThingsYouShouldKnow";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faFileDownload} from "@fortawesome/free-solid-svg-icons";

const Documents = () => {
    const [data] = useContext(EstatePlanContext)

    let documentElement = null

    const setDocumentElement = (element) => (documentElement = element)

    const createPdf = async () => {
        const doc = new jsPDF({
            unit: 'px',
            format: 'letter',
            hotfixes: ['px_scaling'],
        })
        await doc.html(documentElement, { margin: 50 })
        doc.save('sample-letter.pdf')
    }

    const owlText = (
        <>
            Review and save your documents.
        </>
    )

    // TODO Make this a list of documents
    // TODO set(data) should not be called here
    set(data)
    return (
        <>
            <BasePage owlSay={owlText}>
                <CryptoassetsNav />
                <Button onClick={createPdf}>
                    <FontAwesomeIcon icon={faFileDownload} /> Download
                </Button>
                <div className="document" ref={setDocumentElement}>
                    <ReactMarkdown children={CryptoAssetInventoryForm(data)} />
                </div>
                <PrevNextButtons prev={CryptoassetsOtherThingsYouShouldKnow.path} />
            </BasePage>
        </>
    )
}

Documents.path = '/documents'

export default Documents
