import { useEffect } from 'react'
import { useHistory } from 'react-router-dom'

const GA_MEASUREMENT_ID = 'G-QVHLNHGMM7'

export const useTracking = (trackingId = GA_MEASUREMENT_ID) => {
  const { listen } = useHistory()

  useEffect(() => {
    const unlisten = listen((location) => {
      if (!window.gtag) return
      if (!trackingId) {
        console.error(
          'Tracking not enabled, as `trackingId` was not given and there is no `GA_MEASUREMENT_ID`.',
        )
        return
      }

      window.gtag('config', trackingId, { page_path: location.pathname })
    })

    return unlisten
  }, [trackingId, listen])
}

export const sendEvent = (action, category, label, value) =>
  window.gtag('event', action, { category, label, value })

export const sendClick = (category, label, log) =>
  sendEvent('click', category, label, log)
