import React, { useContext, useEffect, useState } from 'react'
import { EstatePlanContext } from '../State/EstatePlanStore'
import SelectFromCollection from './SelectFromCollection'
import EditContact from "../Pages/contacts/EditContact";

const ContactSelector = ({ exclude, ...props }) => {
    const [{ contacts }] = useContext(EstatePlanContext)
    const [orderedOptions, setOrderedOptions] = useState([])
    useEffect(
        () =>
            setOrderedOptions(
                contacts.sort((a, b) => a.fullName?.localeCompare(b.fullName)),
            ),
        [contacts],
    )

    return (
        <SelectFromCollection
            collection={orderedOptions}
            editorPath={EditContact.path}
            textKey="fullName"
            showAddOption={true}
            addNewOptionText="add new contact"
            exclude={exclude}
            {...props}
        />
    )
}

export default ContactSelector
