import React from 'react'
import { FormControl } from 'react-bootstrap'

const hidePassword = ({ target }) => (target.type = 'password')
const showPassword = ({ target }) => (target.type = 'text')

const PasswordControl = (props) => (
    <FormControl
        {...props}
        type="password"
        onBlur={hidePassword}
        onFocus={showPassword}
    />
)

export default PasswordControl
