import {Link} from "react-router-dom";
import React from "react";

const PrevNextButtons = (props) => {
    return (
        <nav aria-label="Page navigation">
            <ul className="pagination pagination justify-content-center mt-4">
                {props.prev && (
                    <li className="page-item">
                        <Link className="page-link" to={props.prev}>
                            Previous
                        </Link>
                    </li>
                )}
                {props.next && (
                    <li className="page-item">
                        <Link className="page-link" to={props.next}>
                            Next
                        </Link>
                    </li>
                )}
            </ul>
        </nav>
    )
}

export default PrevNextButtons
