import React, { useContext } from 'react'
import { EstatePlanContext } from '../../State/EstatePlanStore'
import './CryptoassetTypes.scss'
import { TOGGLE_COIN_TYPE_HELD } from '../../State/EstatePlanReducer'
import CryptoassetTypesSelector from '../../Components/CryptoassetTypesSelector.js'
import BasePage from '../BasePage'
import CoinTypes from "../../State/CoinTypes.json";
import CryptoassetsNav from "./CryptoassetsNav";
import CryptoassetsWallets from './CryptoassetsWallets'
import PrevNextButtons from "../../Components/PrevNextButtons";

const CryptoassetTypes = () => {
    const [
        {
            cryptoassets: { typesHeld },
        },
        dispatch,
    ] = useContext(EstatePlanContext)

    const onToggle = (coinType) => () => {
        dispatch({
            type: TOGGLE_COIN_TYPE_HELD,
            payload: coinType,
        })
    }

    const owlText = <>What kind of cryptoassets do you own?</>

    return (
        <BasePage owlSay={owlText}>
            <CryptoassetsNav />
            <CryptoassetTypesSelector
                all={CoinTypes}
                selected={typesHeld}
                onToggle={onToggle}
            />
            <PrevNextButtons next={CryptoassetsWallets.path} />
        </BasePage>
    )
}

CryptoassetTypes.path = '/cryptoassets/types'

export default CryptoassetTypes
