import React, {useContext, useEffect, useState} from 'react'
import {EstatePlanContext} from '../../State/EstatePlanStore'
import {Button, Form, FormControl, FormGroup, FormLabel} from 'react-bootstrap'
import {ADD_WALLET, REMOVE_WALLET, UPDATE_WALLET_BY_ID} from '../../State/EstatePlanReducer'
import {useParams, useHistory} from 'react-router'
import CryptoassetTypesSelector from '../../Components/CryptoassetTypesSelector.js'
import BasePage from '../BasePage'
import DeviceSelector from '../../Components/DeviceSelector'
import PasswordControl from '../../Components/PasswordControl'
import eventMapper from "../../Helpers/eventMapper"
import byId from "../../Helpers/byId"
import CoinTypes from "../../State/CoinTypes.json";

const CryptoassetsWallet = () => {
    const {id: walletId} = useParams()
    const history = useHistory()

    const [
        {
            cryptoassets: {wallets},
        },
        dispatch,
    ] = useContext(EstatePlanContext)

    const wallet = wallets.find(byId(walletId))
    const {
        name,
        typesHeld: typesHeldInWallet,
        device_id,
        type,
        pin_or_password,
    } = wallet || {}

    useEffect(() => {
        if (!wallet) {
            dispatch({type: ADD_WALLET, id: walletId})
        }
// eslint-disable-next-line
    }, [])

    const [owlText, setOwlText] = useState()

    useEffect(() => {
        setOwlText(
            <>
                Details for <i>{name || 'unnamed'}</i>{' '}
                cryptoasset wallet
            </>,
        )
    }, [name])

    const dispatchUpdate = (path, value) => {
        dispatch({
            type: UPDATE_WALLET_BY_ID,
            id: walletId,
            path: path,
            value: value,
        })
    }

    const onTypeToggle = (coinType) => () => {
        //TODO Move to the reducer
        if (typesHeldInWallet.includes(coinType)) {
            dispatchUpdate(
                'typesHeld',
                typesHeldInWallet.filter((v) => v !== coinType),
            )
        } else {
            dispatchUpdate('typesHeld', [...typesHeldInWallet, coinType])
        }
    }

    const handleDelete = e => {
        dispatch({type: REMOVE_WALLET, id: walletId})
        history.goBack()
    }

    return (
        <BasePage owlSay={owlText}>
            <Form onSubmit={({preventDefault}) => preventDefault()}>
                <FormGroup controlId="name">
                    <FormLabel>Name</FormLabel>
                    <FormControl
                        value={name}
                        onChange={eventMapper(dispatchUpdate)}
                    />
                </FormGroup>
                <FormGroup controlId="typesHeld">
                    <CryptoassetTypesSelector
                        all={CoinTypes}
                        selected={typesHeldInWallet}
                        onToggle={onTypeToggle}
                    />
                </FormGroup>
                <FormGroup controlId="device_id">
                    <FormLabel>
                        Which device does this wallet run on?
                    </FormLabel>
                    <DeviceSelector
                        selected={device_id}
                        onChange={eventMapper(dispatchUpdate)}
                    />
                </FormGroup>
                <FormGroup controlId="type">
                    <FormLabel>What kind of wallet is this?</FormLabel>
                    <FormControl
                        name="type"
                        value={type}
                        onChange={eventMapper(dispatchUpdate)}
                    />
                </FormGroup>
                <FormGroup controlId="pin_or_password">
                    <FormLabel>
                        What is the PIN / Password for this wallet?
                    </FormLabel>
                    <PasswordControl
                        name="pin_or_password"
                        value={pin_or_password}
                        onChange={eventMapper(dispatchUpdate)}
                    />
                </FormGroup>
            </Form>
            <Button variant='outline-danger' onClick={handleDelete}>Delete wallet</Button>
        </BasePage>
    )
}
CryptoassetsWallet.path = '/cryptoassets/wallets/:id'

export default CryptoassetsWallet
