import React, {useContext} from 'react'
import EndowlNavbar from './Components/EndowlNavBar'
import './App.scss'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import Introduction from './Pages/Introduction'
import Cryptoassets from './Pages/cryptoassets/Cryptoassets'
import { useTracking } from './Hooks/useAnalytics'
import BottomNav from './Components/BottomNav'
import OwlSayStore from './State/OwlSayStore'
import CryptoassetTypes from './Pages/cryptoassets/CryptoassetTypes'
import Unimplemented from './Pages/Unimplemented'
import EstatePlanStore from './State/EstatePlanStore'
import CryptoassetsWallets from './Pages/cryptoassets/CryptoassetsWallets'
import CryptoassetsWallet from './Pages/cryptoassets/CryptoassetsWallet'
import CryptoassetsHelpers from './Pages/cryptoassets/CryptoassetsHelpers'
import CryptoassetsOtherThingsYouShouldKnow from './Pages/cryptoassets/CryptoassetsOtherThingsYouShouldKnow'
import EditDevice from './Pages/devices/EditDevice'
import EditContact from './Pages/contacts/EditContact'
import Documents from './Pages/Documents'
import Login from './Pages/Login'
import UserAuthStore, {UserAuthContext} from "./State/UserAuthStore";
import {CookiesProvider} from "react-cookie";

const App = () => {
    useTracking()
    const [userAuthState, ] = useContext(UserAuthContext)
    return (
        <>
            <div className="root-container">
                <EndowlNavbar />

                <div className="main container-fluid">
                    {!userAuthState.loggedIn && (
                        <Login />
                    )}
                    {userAuthState.loggedIn && (
                        <Switch>
                            <Route path={Introduction.path} exact={true}>
                                <Introduction />
                            </Route>
                            <Route path={Documents.path}>
                                <Documents />
                            </Route>
                            <Route path={Cryptoassets.path} exact={true}>
                                <Cryptoassets />
                            </Route>
                            <Route path={CryptoassetTypes.path} exact={true}>
                                <CryptoassetTypes />
                            </Route>
                            <Route path={CryptoassetsWallets.path} exact={true}>
                                <CryptoassetsWallets />
                            </Route>
                            <Route path={CryptoassetsWallet.path} exact={true}>
                                <CryptoassetsWallet />
                            </Route>
                            <Route path={CryptoassetsHelpers.path} exact={true}>
                                <CryptoassetsHelpers />
                            </Route>
                            <Route
                                path={CryptoassetsOtherThingsYouShouldKnow.path}
                                exact={true}>
                                <CryptoassetsOtherThingsYouShouldKnow />
                            </Route>
                            <Route path={EditDevice.path} exact={true}>
                                <EditDevice />
                            </Route>
                            <Route path={EditContact.path} exact={true}>
                                <EditContact />
                            </Route>
                            <Route path={Unimplemented.path}>
                                <Unimplemented />
                            </Route>
                        </Switch>
                    )}
                </div>
                <BottomNav />
            </div>
        </>
    )
}

const AppWrapper = () => (
    <CookiesProvider>
        <UserAuthStore>
            <EstatePlanStore>
                <OwlSayStore>
                    <Router basename='/UKEWABQYWS'>
                        <App />
                    </Router>
                </OwlSayStore>
            </EstatePlanStore>
        </UserAuthStore>
    </CookiesProvider>
)

export default AppWrapper