import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import { EstatePlanContext } from '../../State/EstatePlanStore'
import {
    ADD_CONTACT_TO_HELPER_LIST,
    REMOVE_CONTACT_FROM_HELPER_LIST,
} from '../../State/EstatePlanReducer'
import BasePage from '../BasePage'
import { FormGroup, FormLabel } from 'react-bootstrap'
import ContactSelector from '../../Components/ContactSelector'
import EditContact from '../contacts/EditContact'
import eventMapper from '../../Helpers/eventMapper'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUserSlash } from '@fortawesome/free-solid-svg-icons'
import './CryptoassetsHelpers.scss'
import CryptoassetsNav from "./CryptoassetsNav"
import PrevNextButtons from "../../Components/PrevNextButtons";
import CryptoassetsWallets from "./CryptoassetsWallets";
import CryptoassetsOtherThingsYouShouldKnow from "./CryptoassetsOtherThingsYouShouldKnow";

const CryptoassetsHelpers = () => {
    const [
        {
            contacts,
            cryptoassets: { trustedHelpers },
        },
        dispatch,
    ] = useContext(EstatePlanContext)

    const owlText = (
        <>Who can your heirs ask for help with your cryptocurrency?</>
    )

    const onChange = eventMapper((path, value) =>
        dispatch({ type: ADD_CONTACT_TO_HELPER_LIST, value }),
    )

    const onRemove = id => () =>
        dispatch({ type: REMOVE_CONTACT_FROM_HELPER_LIST, value: id })

    return (
        <BasePage owlSay={owlText}>
            <CryptoassetsNav />
            {trustedHelpers.length === 0 && (
                <h4 className={"text-center mb-4"}>
                    You haven't added any trusted helpers yet.
                </h4>
            )}
            <ul>
                {trustedHelpers.map((helper) => (
                    <li key={helper}>
                        <FontAwesomeIcon
                            icon={faUserSlash}
                            onClick={onRemove(helper)}
                            className="delete-user-icon"
                        />
                        <Link to={EditContact.path.replace(':id', helper)}>
                            {contacts.find((c) => c.id === helper)?.fullName}
                        </Link>
                    </li>
                ))}
            </ul>
            <FormGroup>
                <FormLabel>Add a trusted helper</FormLabel>
                <ContactSelector
                    onChange={onChange}
                    editorPath={EditContact.path}
                    exclude={trustedHelpers}
                />
            </FormGroup>
            <PrevNextButtons prev={CryptoassetsWallets.path} next={CryptoassetsOtherThingsYouShouldKnow.path} />
        </BasePage>
    )
}

CryptoassetsHelpers.path = '/cryptoassets/helpers'

export default CryptoassetsHelpers
