import React from 'react'
import { ReactComponent as Avatar } from '../icons/alfred.svg'
import './OwlSay.scss'

function OwlSay({ children }) {
    return (
        <div className="OwlfredAgent">
            <Avatar className="owlfred" />
            <span className="bubble">{children}</span>
        </div>
    )
}

export default OwlSay
