import React from 'react'
import BasePage from './BasePage'
import MenuButton from '../Components/MenuButton'
import Cryptoassets from './cryptoassets/Cryptoassets'
import { ReactComponent as CryptoassetsIcon } from '../icons/cryptocurrency.svg'

const Introduction = () => {
    const owlText = (
        <>
            Hi. I'm Alfred. I'm here to help you plan your legacy. What's on
            your mind?
        </>
    )

    return (
        <BasePage owlSay={owlText}>
            <div className="menu">
                <MenuButton
                    to={Cryptoassets.path}
                    icon={CryptoassetsIcon}
                    iconAlt="crypto">
                    <p>Crypto</p>
                </MenuButton>
            </div>
        </BasePage>
    )
}

Introduction.path = '/'
export default Introduction
