import React, {useContext, useEffect, useState} from 'react'
import {useParams, useHistory} from 'react-router'
import {Button, Form, FormControl, FormGroup, FormLabel} from 'react-bootstrap'
import {EstatePlanContext} from '../../State/EstatePlanStore'
import BasePage from '../BasePage'
import {ADD_CONTACT, REMOVE_CONTACT, UPDATE_CONTACT_BY_ID,} from '../../State/EstatePlanReducer'
import eventMapper from '../../Helpers/eventMapper'
import byId from "../../Helpers/byId"
import useUniqueDefaultFieldValue from "../../Hooks/useUniqueDefaultFieldValue";
import useHideShowDefault from "../../Hooks/useHideShowDefault";

const EditContact = () => {
    const { id: contactId } = useParams()
    const history = useHistory()
    const [{ contacts }, dispatch] = useContext(EstatePlanContext)
    const [owlText, setOwlText] = useState()

    const contact = contacts.find(byId(contactId))
    const { firstName, fullName, phoneNumber, email } = contact || {}

    useEffect(() => {
        if (!contact) {
            dispatch({ type: ADD_CONTACT, id: contactId })
        }
// eslint-disable-next-line
    }, [])

    // TODO Extract all of this into a single hook, if feasible
    const [defaultName, setDefaultName] = useState(fullName)
    const [getUniqueDefaultValue] = useUniqueDefaultFieldValue(contacts, 'fullName')
    const [hideDefaultName, loadDefaultName] = useHideShowDefault(defaultName, v => dispatchUpdate('fullName', v))

    useEffect(() => {
        if (fullName === defaultName) {
            const newDefaultName = getUniqueDefaultValue(contact, firstName || email || 'new contact')
            if (newDefaultName !== fullName) {
                setDefaultName(newDefaultName)
                dispatchUpdate('fullName', newDefaultName)
            }
        }
// eslint-disable-next-line
    }, [firstName, email])
    // End of TODO

    useEffect(() => {
        setOwlText(<>Details for {fullName || 'a new contact'}</>)
    }, [fullName])

    const dispatchUpdate = (path, value) =>
        dispatch({
            type: UPDATE_CONTACT_BY_ID,
            id: contactId,
            path: path,
            value: value,
        })

    const handleDelete = e => {
        dispatch({type: REMOVE_CONTACT, id: contactId})
        history.goBack()
    }

    return (
        <BasePage owlSay={owlText}>
            <Form onSubmit={({ preventDefault }) => preventDefault()}>
                <FormGroup controlId="firstName">
                    <FormLabel>First Name</FormLabel>
                    <FormControl value={firstName || ''} onChange={eventMapper(dispatchUpdate)} />
                </FormGroup>
                <FormGroup controlId="fullName">
                    <FormLabel>Full Name</FormLabel>
                    <FormControl value={fullName || ''} onChange={eventMapper(dispatchUpdate)} onBlur={loadDefaultName} onFocus={hideDefaultName}/>
                </FormGroup>
                <FormGroup controlId="phoneNumber">
                    <FormLabel>Phone Number</FormLabel>
                    <FormControl
                        value={phoneNumber || ''}
                        onChange={eventMapper(dispatchUpdate)}
                    />
                </FormGroup>
                <FormGroup controlId="email">
                    <FormLabel>Email</FormLabel>
                    <FormControl value={email || ''} onChange={eventMapper(dispatchUpdate)} />
                </FormGroup>
            </Form>
            <Button variant='outline-danger' onClick={handleDelete}>Delete contact</Button>
        </BasePage>
    )
}

EditContact.path = '/contacts/:id'

export default EditContact
