import React, { useContext, useEffect } from 'react'
import { OwlSayContext } from '../State/OwlSayStore'
import { SET_DIALOG } from '../State/OwlSayReducer'
import { useHistory } from 'react-router-dom'

const BasePage = ({ children, owlSay }) => {
    const {
        location: { pathname },
    } = useHistory()

    // const { setPathPageView } = useTrackingCode()
    // setPathPageView(pathname)

    const [, dispatchOwl] = useContext(OwlSayContext)

    useEffect(() => {
        dispatchOwl({
            type: SET_DIALOG,
            payload: owlSay,
        })
// eslint-disable-next-line
    }, [owlSay])

    return (
        <section className="page">
            {children}
        </section>
    )
}
export default BasePage
