import React, { createContext, useReducer } from 'react'
import OwlSayReducer from './OwlSayReducer'

const initialState = {
    dialog: 'Hi. 👋',
}

export const OwlSayContext = createContext()

const OwlSayStore = ({ children }) => {
    const [state, dispatch] = useReducer(OwlSayReducer, initialState)
    return (
        <OwlSayContext.Provider value={[state, dispatch]}>
            {children}
        </OwlSayContext.Provider>
    )
}
export default OwlSayStore
