import React from 'react'
import { useHistory } from 'react-router-dom'
import uuid from 'react-uuid'
import { FormControl } from 'react-bootstrap'

const ADD_NEW_ITEM = 'ADD_NEW_ITEM'

const SelectFromCollection = ({
    collection,
    editorPath,
    selected,
    onChange,
    textKey,
    showAddOption,
    addNewOptionText,
    exclude,
    id,
    ...props
}) => {
    const history = useHistory()
    const onSelectionChange = (e) => {
        const {
            target: { value },
        } = e
        if (value === ADD_NEW_ITEM) {
            const newItemId = uuid()
            onChange &&
                onChange({ ...e, target: { ...e.target, value: newItemId, id } })
            history.push(editorPath.replace(':id', newItemId))
        } else {
            onChange(e)
        }
    }

    return (
        <>
            <FormControl
                {...props}
                as="select"
                onChange={onSelectionChange}
                value={selected || ''}>
                <option value="" hidden>
                    Select...
                </option>
                {collection.map(
                    ({ id, ...item }) =>
                        (!exclude || !exclude.find((i) => i === id)) && (
                            <option key={id} value={id}>
                                {item[textKey]}
                            </option>
                        ),
                )}
                {showAddOption && (
                    <option value={ADD_NEW_ITEM} key={ADD_NEW_ITEM}>
                        -- {addNewOptionText || 'Add'} --
                    </option>
                )}
            </FormControl>
        </>
    )
}

export default SelectFromCollection
