import React from 'react'
import { camelCase } from 'lodash'
import PictureCheckbox from './PictureCheckbox'

const CryptoassetTypesSelector = ({ all, selected, onToggle }) => {
    return (
        <div className="picture-checkbox-container">
            {all &&
                all.map((coin, i) => {
                    const coinType = camelCase(coin)
                    return (
                        <PictureCheckbox
                            key={i}
                            className={coinType}
                            value={selected && selected.includes(coin)}
                            onToggle={onToggle(coin)}>
                            <span>{coin}</span>
                        </PictureCheckbox>
                    )
                })}
        </div>
    )
}

export default CryptoassetTypesSelector
