import {Button, Form} from "react-bootstrap";
import BasePage from "./BasePage";
import React, {useContext, useEffect, useState} from "react";
import { UserAuthContext, LOCAL_PASSWORD_COOKIE_KEY } from '../State/UserAuthStore'
import {SET_LOCAL_PASSWORD_AND_LOGIN} from "../State/UserAuthReducer";
import {EstatePlanContext, EstatePlanDataStorageKey} from "../State/EstatePlanStore";
import {LOAD_STORED_DATA, WIPE_ESTATE_DATA} from "../State/EstatePlanReducer";
import CryptoJs from "crypto-js";
import { isNull } from 'lodash'
import { useCookies } from 'react-cookie'

const Login = () => {
    const [userAuthState, dispatchUserAuthState] = useContext(UserAuthContext)
    const [, dispatchEstatePlanState] = useContext(EstatePlanContext)

    const [password, setPassword] = useState("")
    const [passwordAgain, setPasswordAgain] = useState("")
    const [loginFailed, setLoginFailed] = useState(false)
    const [stayLoggedIn, setStayLoggedIn] = useState(false)
    const [estateDataExists, ] = useState(!isNull(localStorage.getItem(EstatePlanDataStorageKey)))

    const [cookies, setCookie, removeCookie] = useCookies([LOCAL_PASSWORD_COOKIE_KEY])

    // Display different welcome message for first time vs returning users
    const owlText = estateDataExists ? (
        <>
            Welcome Back!<br />
            Unlock your account to continue.
        </>
    ) : (
        <>
            Welcome! Let's get you set up.<br />
            First, create a password to secure your private information.
        </>
    )

    // Hash user's password with a salt to produce an encryption key called "localPassword" which
    // is used to encrypt/decrypt estate data in local storage
    // Return hash of userPassword + salt
    const hashUserPasswordToLocalPassword = (userPassword, salt) => {
        return CryptoJs.SHA3(userPassword + salt).toString()
    }

    const testDecryptLocalData = (localPassword) => {
        let decryptionSuccessful = false
        try {
            const cyphertext = localStorage.getItem(EstatePlanDataStorageKey)
            // console.log("testing cyphertext: ", cyphertext)
            if (cyphertext && cyphertext !== 'null') {
                const json = CryptoJs.AES.decrypt(cyphertext, localPassword).toString(CryptoJs.enc.Utf8)
                JSON.parse(json)
                decryptionSuccessful = true
                console.log("Login successful!")   // TODO: Remove this
            }
        } catch (e) {
            console.log("ERR during login while decrypting locally stored state")    // TODO: Remove this
            decryptionSuccessful = false
        }
        return decryptionSuccessful
    }

    const handleLogin = (e) => {
        e.preventDefault()
        console.log("Logging in")

        const localPassword = hashUserPasswordToLocalPassword(password, userAuthState.salt)
        const decryptionSuccessful = testDecryptLocalData(localPassword)

        if(decryptionSuccessful) {
            // Decryption of local estate store succeeded, set user as authenticated
            dispatchUserAuthState({
                type: SET_LOCAL_PASSWORD_AND_LOGIN,
                payload: localPassword,
            })

            dispatchEstatePlanState({
                type: LOAD_STORED_DATA,
                password: localPassword
            })

            if(stayLoggedIn) {
                // Store localPassword (hashed password+salt) in a cookie
                // TODO: Set short lived expiration time on cookie
                setCookie(LOCAL_PASSWORD_COOKIE_KEY, localPassword, { path: '/' });
            } else {
                removeCookie(LOCAL_PASSWORD_COOKIE_KEY, { path: '/' })
            }

        } else {
            // Decryption of local estate store failed
            console.log("ERR failed to decrypt and parse stored data")    // TODO: Remove this
            setLoginFailed(true)
        }
    }

    const handleNewUser = (e) => {
        e.preventDefault()
        console.log("Creating new user")
        setLoginFailed(false)

        if(password === passwordAgain && password.length > 0) {
            // Passwords match and are at least 1 character long
            // Set user as logged in and save the hash of their password, which is used to decrypt local estate data
            const localPassword = hashUserPasswordToLocalPassword(password, userAuthState.salt)
            dispatchUserAuthState({
                type: SET_LOCAL_PASSWORD_AND_LOGIN,
                payload: localPassword,
            })
            // Initialize estate plan data
            dispatchEstatePlanState({
                type: WIPE_ESTATE_DATA,
            })

            if(stayLoggedIn) {
                // Store localPassword (hashed password+salt) in a cookie
                // TODO: Set short lived expiration time on cookie
                setCookie(LOCAL_PASSWORD_COOKIE_KEY, localPassword, { path: '/' });
            } else {
                removeCookie(LOCAL_PASSWORD_COOKIE_KEY, { path: '/' })
            }
        } else {
            setLoginFailed(true)
        }
    }

    useEffect(() => {
        if(!userAuthState.loggedIn) {}
    }, [userAuthState])

    useEffect(() => {
        // On first load check for cookie containing local_password, if found save to userAuthState.localPassword and set user logged in
        if(cookies[LOCAL_PASSWORD_COOKIE_KEY]) {
            const decryptionSuccessful = testDecryptLocalData(cookies[LOCAL_PASSWORD_COOKIE_KEY])

            if(decryptionSuccessful) {
                // Decryption of local estate store succeeded, set user as authenticated
                const localPassword = cookies[LOCAL_PASSWORD_COOKIE_KEY]

                dispatchUserAuthState({
                    type: SET_LOCAL_PASSWORD_AND_LOGIN,
                    payload: localPassword,
                })

                dispatchEstatePlanState({
                    type: LOAD_STORED_DATA,
                    password: localPassword
                })
            }
            // TODO: reset the cookie expiration?
        }
        // eslint-disable-next-line
    }, [])

    return (
        <BasePage owlSay={owlText}>
            {userAuthState.loggedIn && (
                <h3>Logged In!</h3>
            )}

            {estateDataExists && (
                <Form onSubmit={handleLogin}>
                    <Form.Group controlId="formBasicPassword">
                        <Form.Label>Password</Form.Label>
                        <Form.Control type="password" placeholder="Password" value={password} onChange={e => setPassword(e.target.value)} />
                    </Form.Group>
                    {loginFailed && (
                        <p className="text-danger">Login Failed</p>
                    )}
                    <Form.Group controlId="formBasicCheckbox">
                        <Form.Check type="switch" label="Keep me logged in" checked={stayLoggedIn} onChange={(e) => setStayLoggedIn(e.target.checked)} />
                    </Form.Group>
                    <Button variant="primary" type="submit">
                        Submit
                    </Button>
                </Form>
            )}
            {!estateDataExists && (
                <Form onSubmit={handleNewUser}>
                    <Form.Group controlId="formBasicPasswordNew">
                        <Form.Label>Password</Form.Label>
                        <Form.Control type="password" placeholder="Password" value={password} onChange={e => setPassword(e.target.value)} />
                    </Form.Group>
                    <Form.Group controlId="formBasicPasswordAgain">
                        <Form.Label>Password Again</Form.Label>
                        <Form.Control type="password" placeholder="Password" value={passwordAgain} onChange={e => setPasswordAgain(e.target.value)} />
                    </Form.Group>
                    {loginFailed && (
                        <p className="text-danger">Login Failed</p>
                    )}
                    {(password !== passwordAgain) && (
                        <p className="text-danger">Passwords Don't Match</p>
                    )}
                    <Form.Group controlId="formBasicCheckboxNew">
                        <Form.Check type="switch" label="Keep me logged in" checked={stayLoggedIn} onChange={(e) => setStayLoggedIn(e.target.checked)} />
                    </Form.Group>
                    <Button variant="primary" type="submit">
                        Submit
                    </Button>
                </Form>

            )}
        </BasePage>
    )
}

Login.path = '/login'

export default Login
