import React, { useContext, useEffect, useState } from 'react'
import {Button, FormCheck, FormControl, FormGroup, FormLabel} from 'react-bootstrap'
import { EstatePlanContext } from '../../State/EstatePlanStore'
import {UPDATE_DEVICE_BY_ID, ADD_DEVICE, REMOVE_DEVICE} from '../../State/EstatePlanReducer'
import BasePage from '../BasePage'
import {useParams, useHistory} from 'react-router'
import PasswordControl from '../../Components/PasswordControl'
import byId from "../../Helpers/byId";
import useUniqueDefaultFieldValue from "../../Hooks/useUniqueDefaultFieldValue";
import useHideShowDefault from "../../Hooks/useHideShowDefault";

const PASSWORD = 'password'
const PIN = 'pin'

const EditDevice = () => {
    const { id: deviceId } = useParams()
    const history = useHistory()
    const [{devices}, dispatch] = useContext(EstatePlanContext)
    const [owlText, setOwlText] = useState()
    const [unlockMethod, setUnlockMethod] = useState()

    const device = devices.find(byId(deviceId))
    const { name, type, host_name, login, pin, location } = device || {}

    useEffect(() => {
        if (!device) {
            dispatch({ type: ADD_DEVICE, id: deviceId })
        }
// eslint-disable-next-line
    }, [])

    // TODO Extract all of this into a single hook, if feasible
    const [defaultName, setDefaultName] = useState(name)
    const [getUniqueDefaultValue] = useUniqueDefaultFieldValue(devices, 'name')
    const [hideDefaultName, loadDefaultName] = useHideShowDefault(defaultName, v => dispatchUpdate('name', v))

    useEffect(() => {
        if (name === defaultName) {
            const newDefaultName = getUniqueDefaultValue(device, type || 'new device')
            if (newDefaultName !== name) {
                setDefaultName(newDefaultName)
                dispatchUpdate('name', newDefaultName)
            }
        }
// eslint-disable-next-line
    }, [type])
    // End of TODO

    useEffect(() => {
        setOwlText(`details about ${name || 'unnamed'}`)
        setUnlockMethod(pin ? PIN : PASSWORD)
    }, [name, pin])

    const onChange = ({ target: { id, value } }) => dispatchUpdate(id, value)

    const dispatchUpdate = (inputId, value) => {
        dispatch({
            type: UPDATE_DEVICE_BY_ID,
            id: deviceId,
            path: inputId,
            value,
        })
    }

    const onPinOrPasswordSelect = method => () => {
        if (method === PIN) {
            dispatchUpdate('login', { username: '', password: '' })
        }
        if (method === PASSWORD) {
            dispatchUpdate('pin', '')
        }
        setUnlockMethod(method)
    }

    const handleDelete = e => {
        dispatch({type: REMOVE_DEVICE, id: deviceId})
        history.goBack()
    }

    return (
        <>
            {!deviceId && <div>Device id not specified</div>}
            {deviceId && (
                <BasePage owlSay={owlText}>
                    <FormGroup controlId="name">
                        <FormLabel>
                            What do you want to call this device?
                        </FormLabel>
                        <FormControl
                            type="text"
                            value={name}
                            onChange={onChange}
                            onFocus={hideDefaultName}
                            onBlur={loadDefaultName}
                        />
                    </FormGroup>
                    <FormGroup controlId="type">
                        <FormLabel>What type of device is it?</FormLabel>
                        <FormControl
                            as="select"
                            value={type}
                            onChange={onChange}>
                            <option value="laptop">Laptop Computer</option>
                            <option value="desktop">Desktop Computer</option>
                            <option value="phone">Phone</option>
                            <option value="tablet">Tablet</option>
                            <option value="hardware wallet">
                                Hardware wallet
                            </option>
                        </FormControl>
                    </FormGroup>
                    <FormGroup controlId="host_name">
                        <FormLabel>
                            Additional information to identify this device?
                        </FormLabel>
                        <FormControl
                            type="text"
                            value={host_name || ''}
                            onChange={onChange}
                        />
                    </FormGroup>
                    <FormGroup controlId="pinOrPassword">
                        <FormLabel>
                            How do you unlock access to this device?
                        </FormLabel>
                        <FormCheck
                            type="radio"
                            label="Password"
                            checked={unlockMethod === PASSWORD}
                            onChange={onPinOrPasswordSelect(PASSWORD)}
                        />
                        <FormCheck
                            type="radio"
                            label="PIN"
                            checked={unlockMethod === PIN}
                            onChange={onPinOrPasswordSelect(PIN)}
                        />
                    </FormGroup>
                    {unlockMethod === PIN && (
                        <FormGroup controlId="pin">
                            <FormLabel>PIN</FormLabel>
                            <PasswordControl
                                type="password"
                                value={pin || ''}
                                onChange={onChange}
                            />
                        </FormGroup>
                    )}
                    {unlockMethod === PASSWORD && (
                        <>
                            <FormGroup controlId="login.username">
                                <FormLabel>User name</FormLabel>
                                <FormControl
                                    type="text"
                                    value={(login && login.username) || ''}
                                    onChange={onChange}
                                />
                            </FormGroup>
                            <FormGroup controlId="login.password">
                                <FormLabel>Password</FormLabel>
                                <PasswordControl
                                    type="password"
                                    value={(login && login.password) || ''}
                                    onChange={onChange}
                                />
                            </FormGroup>
                        </>
                    )}
                    <FormGroup controlId="location">
                        <FormLabel>
                            Where is this device usually kept?
                        </FormLabel>
                        <FormControl
                            as="textarea"
                            value={location || ''}
                            onChange={onChange}
                        />
                    </FormGroup>
                    <Button variant='outline-danger' onClick={handleDelete}>Delete device</Button>
                </BasePage>
            )}
        </>
    )
}

EditDevice.path = '/devices/:id'

export default EditDevice
