import {Dropdown, Nav, NavItem} from 'react-bootstrap'
import { Link } from 'react-router-dom'
import Introduction from '../Pages/Introduction'
import React, {useContext} from 'react'
// import {ReactComponent as ListIcon} from '../icons/List.svg'
import {ReactComponent as ConnectIcon} from '../icons/connect.svg'
import {ReactComponent as DocumentsIcon} from '../icons/document.svg'
import Documents from '../Pages/Documents'
import ConnectWithProfessionals from '../Pages/ConnectWithProfessionals'
import { ReactComponent as Avatar } from '../icons/alfred.svg'
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCogs, faTrash} from "@fortawesome/free-solid-svg-icons";
import './BottomNav.scss'
import {EstatePlanContext, EstatePlanDataStorageKey} from "../State/EstatePlanStore";
import {WIPE_ESTATE_DATA} from "../State/EstatePlanReducer";
import {UserAuthContext, LOCAL_PASSWORD_COOKIE_KEY} from "../State/UserAuthStore";
import {LOCK_USER} from "../State/UserAuthReducer";
import {useCookies} from "react-cookie";

const BottomNav = () => {

    const [estatePlanState, dispatchEstatePlanState] = useContext(EstatePlanContext)
    const [, dispatchUserAuthState] = useContext(UserAuthContext)

    const [, , removeCookie] = useCookies([LOCAL_PASSWORD_COOKIE_KEY])

    const handleClearData = () => {
        dispatchEstatePlanState({type: WIPE_ESTATE_DATA})
    }

    const handleDeleteLocalstore = () =>  {
        localStorage.removeItem(EstatePlanDataStorageKey)
    }

    // Print estate object data to console for debugging
    const handleDataToConsole = () => {
        console.log(estatePlanState)
    }

    // Print JSON stringified estate data to console for debugging
    const handleJsonToConsole = () => {
        console.log(JSON.stringify(estatePlanState))
    }

    const handleLockUser = () => {
        removeCookie(LOCAL_PASSWORD_COOKIE_KEY, { path: '/' })
        dispatchUserAuthState({type: LOCK_USER})
    }

    return (
        <Nav className="bottom-nav d-flex justify-content-around">
            <NavItem>
                <Link to={Introduction.path}>
                    <Avatar className="nav-img" />
                </Link>
            </NavItem>
            {/*
            <NavItem>
                <ListIcon />
            </NavItem>
            */}
            <NavItem>
                <Link to={Documents.path}>
                    <DocumentsIcon className="nav-img" />
                </Link>
            </NavItem>
            <NavItem>
                <Link to={ConnectWithProfessionals.path}>
                    <ConnectIcon className="nav-img" />
                </Link>
            </NavItem>
            <Dropdown as={NavItem}>
                <Dropdown.Toggle as={Link} to="#">
                    <FontAwesomeIcon size="3x" icon={faCogs} className="nav-img" />
                </Dropdown.Toggle>
                <Dropdown.Menu>
                    <Dropdown.Item onClick={handleClearData} className="text-danger">
                        <FontAwesomeIcon icon={faTrash} /> Re-Initialize Estate
                    </Dropdown.Item>
                    <Dropdown.Item onClick={handleDeleteLocalstore} className="text-danger">
                        <FontAwesomeIcon icon={faTrash} /> Delete localStore
                    </Dropdown.Item>
                    <Dropdown.Item onClick={handleDataToConsole}>Print Data to Console</Dropdown.Item>
                    <Dropdown.Item onClick={handleJsonToConsole}>JSON Data to Console</Dropdown.Item>
                    <Dropdown.Item disabled>Load Sample Data</Dropdown.Item>
                    <Dropdown.Divider />
                    <Dropdown.Item onClick={handleLockUser}>Lock</Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>
        </Nav>
    )
}

export default BottomNav
