// eslint-disable-next-line
export default (collection, field) => {
    const getUniqueDefaultValue = (current, candidateDefaultValue) => {
        let trial = candidateDefaultValue
        let i = 0
        const byField = d => d[field] === trial;
        let c = collection?.find(byField)
        // eslint-disable-next-line
        while (c) {
            if (current?.id === c?.id) {
                break // use the same default value that is already set
            }
            trial = `${candidateDefaultValue} (${++i})`
            c = collection.find(byField)
        }
        return trial
    }

    return [getUniqueDefaultValue]
}