import { get, isArrayLike, isObjectLike } from 'lodash'
import CryptoJs from "crypto-js";
import {EstatePlanDataStorageKey, initialState} from "./EstatePlanStore";

const DEFAULT_NEW_WALLET = {
    name: '',
    typesHeld: [],
    device_id: '',
    pin_or_password: '',
    type: '',
}

const DEFAULT_NEW_DEVICE = {
    name: '',
    type: '',
    host_name: '',
    location: '',
    login: {
        username: '',
        password: '',
    },
    pin: '',
}

const DEFAULT_NEW_CONTACT = {
    firstName: '',
    fullName: '',
    address: {
        address1: '',
        address2: '',
        city: '',
        state: '',
        zipCode: '',
    },
    phoneNumber: '',
    email: '',
}

// paths
const PATH_TO_CRYPTOASSET_HELPER_COLLECTION = 'cryptoassets.trustedHelpers'
const PATH_TO_DEVICE_COLLECTION = 'devices';
const PATH_TO_CONTACT_COLLECTION = 'contacts';
const PATH_TO_WALLET_COLLECTION = 'cryptoassets.wallets';

// actions
export const TOGGLE_COIN_TYPE_HELD = 'TOGGLE_COIN_TYPE_HELD'
export const ADD_WALLET = 'ADD_WALLET'
export const ADD_CONTACT = 'ADD_CONTACT'
export const UPDATE_BY_PATH = 'UPDATE_BY_PATH'
export const UPDATE_DEVICE_BY_ID = 'UPDATE_DEVICE_BY_ID'
export const UPDATE_WALLET_BY_ID = 'UPDATE_WALLET_BY_ID'
export const ADD_DEVICE = 'ADD_DEVICE'
export const UPDATE_CONTACT_BY_ID = 'UPDATE_CONTACT_BY_ID'
export const ADD_CONTACT_TO_HELPER_LIST = 'ADD_CONTACT_TO_HELPER_LIST'
export const REMOVE_CONTACT_FROM_HELPER_LIST = 'REMOVE_CONTACT_FROM_HELPER_LIST'
export const REMOVE_WALLET = 'REMOVE_WALLET'
export const REMOVE_DEVICE = 'REMOVE_DEVICE'
export const REMOVE_CONTACT = 'REMOVE_CONTACT'
export const WIPE_ESTATE_DATA = 'WIPE_ESTATE_DATA'
export const LOAD_STORED_DATA = 'LOAD_STORED_DATA'

// non-deterministic
function toggleTypesHeld(state, action) {
    const typesHeld = get(state, 'cryptoassets.typesHeld', [])
    if (typesHeld.includes(action.payload)) {
        // remove it
        return {
            ...state,
            cryptoassets: {
                ...state.cryptoassets,
                typesHeld: typesHeld.filter((v) => v !== action.payload),
            },
        }
    } else {
        // add it
        return {
            ...state,
            cryptoassets: {
                ...state.cryptoassets,
                typesHeld: [...typesHeld, action.payload],
            },
        }
    }
}

const updateByPath = ({ path, value }, state) => {
    var s = { ...state }
    var pathNodes = path.split('.')
    var lastNode = pathNodes.pop()

    pathNodes.reduce((a, p) => {
        if (isObjectLike(a[p])) {
            if (isArrayLike(a[p])) {
                a[p] = [...a[p]]
            } else {
                a[p] = { ...a[p] }
            }
        }
        return a[p]
    }, s)[lastNode] = value
    return s
}

const addEntryToCollection = (collectionPath, defaultItem) => (
    { id },
    state,
) => {
    if (!id) console.log('id must be set')
    const collection = get(state, collectionPath)
    if (collection.find((c) => c.id === id)) {
        console.error(
            `Tried to add a new item with the existing id ${id} to the collection at ${collectionPath}`,
        )
        return state
    }
    return updateByPath(
        {
            path: collectionPath,
            value: [...collection, { ...defaultItem, id }],
        },
        state,
    )
}

const addDevice = addEntryToCollection(PATH_TO_DEVICE_COLLECTION, DEFAULT_NEW_DEVICE)
const addContact = addEntryToCollection(PATH_TO_CONTACT_COLLECTION, DEFAULT_NEW_CONTACT)
const addWallet = addEntryToCollection(
    PATH_TO_WALLET_COLLECTION,
    DEFAULT_NEW_WALLET,
)
const updateItemInCollection = (collectionPath) => (
    { id, path, value },
    state,
) => {
    const collection = get(state, collectionPath, [])
    const item = collection.find((c) => c.id === id)
    const newItem = updateByPath({ path, value }, item)
    const newCollection = [...collection.filter(i => i.id !== id), newItem]
    return updateByPath({ path: collectionPath, value: newCollection }, state)
}

const updateDeviceById = updateItemInCollection(PATH_TO_DEVICE_COLLECTION)
const updateWalletById = updateItemInCollection(PATH_TO_WALLET_COLLECTION)
const updateContactById = updateItemInCollection(PATH_TO_CONTACT_COLLECTION)

const addNewHelper = ({ value: helperId }, state) => {
    const trustedHelpers = get(state, PATH_TO_CRYPTOASSET_HELPER_COLLECTION)
    if (trustedHelpers.find((h) => h === helperId)) {
        return state
    }
    return updateByPath(
        {
            path: PATH_TO_CRYPTOASSET_HELPER_COLLECTION,
            value: [...trustedHelpers, helperId],
        },
        state,
    )
}

const removeItemFromCollection = (path) => ({ id }, state) => {
    const collection = get(state, path)
    return updateByPath(
        {
            path: path,
            value: [...collection.filter(h => h.id !== id)],
        },
        state,
    )
}

const removeDevice = removeItemFromCollection(PATH_TO_DEVICE_COLLECTION) // TODO also remove references from wallets
const removeContact = removeItemFromCollection(PATH_TO_CONTACT_COLLECTION) // TODO also remove from helper list
const removeWallet = removeItemFromCollection(PATH_TO_WALLET_COLLECTION)

const removeHelper = ({ value: helperId }, state) => {
    const trustedHelpers = get(state, PATH_TO_CRYPTOASSET_HELPER_COLLECTION)
    return updateByPath(
        {
            path: PATH_TO_CRYPTOASSET_HELPER_COLLECTION,
            value: [...trustedHelpers.filter((h) => h !== helperId)],
        },
        state,
    )
}

const wipeEstate = (action, state) => {
    return initialState
}

const loadStoredData = (action, state) => {
    // Load state from localstorage
    try {
        const cyphertext = localStorage.getItem(EstatePlanDataStorageKey)
        if (cyphertext && cyphertext !== 'null') {
            const json = CryptoJs.AES.decrypt(cyphertext, action.password).toString(CryptoJs.enc.Utf8)
            return JSON.parse(json)
        } else {
            return {...initialState}
        }
    } catch (e) {
        console.log("ERR while decrypting locally stored state")    // TODO: Remove this
        return initialState
    }

}

const EstatePlanReducer = (state, action) => {
    if (!action.type)
        console.error('Reducer called without action.type specified')
    switch (action.type) {
        case TOGGLE_COIN_TYPE_HELD:
            return toggleTypesHeld(state, action)
        case ADD_WALLET:
            return addWallet(action, state)
        case ADD_CONTACT:
            return addContact(action, state)
        case ADD_DEVICE:
            return addDevice(action, state)
        case UPDATE_BY_PATH:
            return updateByPath(action, state)
        case UPDATE_DEVICE_BY_ID:
            return updateDeviceById(action, state)
        case UPDATE_WALLET_BY_ID:
            return updateWalletById(action, state)
        case UPDATE_CONTACT_BY_ID:
            return updateContactById(action, state)
        case ADD_CONTACT_TO_HELPER_LIST:
            return addNewHelper(action, state)
        case REMOVE_CONTACT_FROM_HELPER_LIST:
            return removeHelper(action, state)
        case REMOVE_WALLET:
            return removeWallet(action, state)
        case REMOVE_DEVICE:
            return removeDevice(action, state)
        case REMOVE_CONTACT:
            return removeContact(action, state)
        case WIPE_ESTATE_DATA:
            return wipeEstate(action, state)
        case LOAD_STORED_DATA:
            return loadStoredData(action, state)
        default:
            return state
    }
}
export default EstatePlanReducer
