import React, { useContext } from 'react'
import { Navbar } from 'react-bootstrap'
import './EndowlNavBar.scss'
import OwlSay from './OwlSay'
import { OwlSayContext } from '../State/OwlSayStore'

const EndowlNavbar = () => {
    const [state] = useContext(OwlSayContext)
    return (
        <Navbar>
            <OwlSay>{state.dialog}</OwlSay>
        </Navbar>
    )
}

export default EndowlNavbar
