import React, { useContext, useEffect, useState } from 'react'
import { EstatePlanContext } from '../State/EstatePlanStore'
import EditDevice from '../Pages/devices/EditDevice'
import SelectFromCollection from './SelectFromCollection'

const DeviceSelector = (props) => {
    const [{ devices }] = useContext(EstatePlanContext)
    const [orderedOptions, setOrderedOptions] = useState([])
    useEffect(
        () =>
            setOrderedOptions(
                devices.sort((a, b) => a.name?.localeCompare(b.name)),
            ),
        [devices],
    )

    return (
        <SelectFromCollection
            {...props}
            collection={orderedOptions}
            editorPath={EditDevice.path}
            textKey="name"
            showAddOption={true}
            addNewOptionText="add new device"
            id="device_id"
        />
    )
}

export default DeviceSelector
