import React from 'react'
import { default as Puzzle } from '../icons/puzzle.svg'
import './Unimplemented.scss'
import BasePage from './BasePage'

const Unimplemented = () => {
    const owlText = <>Ah snap!</>

    return (
        <BasePage owlSay={owlText}>
            <div id="unimplemented">
                <h3>That hasn't been implemented yet</h3>
                <Puzzle />
            </div>
        </BasePage>
    )
}

Unimplemented.path = '/unimplemented'

export default Unimplemented
