export const SET_LOCAL_PASSWORD = 'SET_LOCAL_PASSWORD'
export const SET_LOCAL_PASSWORD_AND_LOGIN = 'SET_LOCAL_PASSWORD_AND_LOGIN'
export const LOCK_USER = 'LOCK_USER'

const UserAuthReducer = (state, action) => {
    switch (action.type) {
        case SET_LOCAL_PASSWORD:
            return {
                ...state,
                loggedIn: false,
                localPassword: action.payload,
            }
        case SET_LOCAL_PASSWORD_AND_LOGIN:
            return {
                ...state,
                loggedIn: true,
                localPassword: action.payload,
            }
        case LOCK_USER:
            return {
                ...state,
                loggedIn: false,
                localPassword: null,
            }
        default:
            return state
    }
}
export default UserAuthReducer
