import React, {createContext, useReducer, useEffect, useContext} from 'react'
import EstatePlanReducer from './EstatePlanReducer'
import CryptoJs from 'crypto-js'
import {UserAuthContext} from "./UserAuthStore";

export const initialState = {
    grantor: {},
    cryptoassets: {
        wallets: [],
        trustedHelpers: [],
        typesHeld: [],
        otherInformation: '',
    },
    contacts: [],
    devices: [],
    documents: [],
}

export const EstatePlanContext = createContext()

export const EstatePlanDataStorageKey = 'estate_plan_data'

const EstatePlanStore = ({ children }) => {

    const [userAuthState, ] = useContext(UserAuthContext)

    const [state, reducer] = useReducer(EstatePlanReducer, initialState, () => {
        try {
            const cyphertext = localStorage.getItem(EstatePlanDataStorageKey)
            if (cyphertext && cyphertext !== 'null') {
                const json = CryptoJs.AES.decrypt(cyphertext, userAuthState.localPassword).toString(CryptoJs.enc.Utf8)
                return JSON.parse(json)
            } else {
                return {...initialState}
            }
        } catch (e) {
            return initialState
        }
    })

    useEffect(() => {
        // Only update local storage if user is logged in and an encryption password is set
        if(userAuthState.loggedIn && userAuthState.localPassword !== null) {
            const json = JSON.stringify(state);
            const encrypted = CryptoJs.AES.encrypt(json, userAuthState.localPassword).toString()
            localStorage.setItem(EstatePlanDataStorageKey, encrypted)
        }
    }, [state, userAuthState])

    return (
        <EstatePlanContext.Provider value={[state, reducer]}>
            {children}
        </EstatePlanContext.Provider>
    )
}

export default EstatePlanStore
